.mt {
  &-1 {
    margin-top: $spacing;
  }
  &-2 {
    margin-top: $spacing * 2;
  }
  &-3 {
    margin-top: $spacing * 3;
  }
  &-4 {
    margin-top: $spacing * 4;
  }
  &-5 {
    margin-top: $spacing * 5;
  }
  &-6 {
    margin-top: $spacing * 6;
  }
  &-7 {
    margin-top: $spacing * 10;
  }
  &-8 {
    margin-top: $spacing * 12;
  }
}

